<template>
  <div class="ranking">
    <div class="header clearfix">
      <div class="logo" @click="onClickLeft">
        <van-icon name="arrow-left" class="icon" />
        <span>排行榜</span>
      </div>
      <van-tabs
        class="top"
        type="card"
        @change="change"
        color="#ed424b"
        background="#fff"
        swipeable
        v-model="isSex"
      >
        <van-tab title="男生">
          <div class="maleRangk">
            <van-sidebar class="fl left" v-model="activeKey">
              <van-sidebar-item
                :class="active == index ? 'active' : ''"
                v-for="(item,index) in tabData.male"
                :key="index"
                :title="item.shortTitle"
                @click="tabIndex(index)"
              />
            </van-sidebar>
            <div class="fr right">
              <div
                class="ranking-item clearfix"
                v-for="(item,index) in maleData"
                :key="index"
                @click="hotClick(item)"
              >
                <div class="ranking-img fl">
                  <img class="auto-img" v-lazy="item.cover" alt />
                </div>
                <div class="ranking-detail fl">
                  <div class="ranking-title one-text">
                    <van-tag
                      class="tag"
                      round
                      size="medium"
                      :type="index + 1 == 1 ?  'danger' : index + 1 == 2 ? 'warning' : index + 1 == 3 ? 'success' : 'default'"
                    >{{index + 1}}</van-tag>
                    {{item.title}}
                  </div>
                  <div class="ranking-shortIntro">简介：{{item.shortIntro}}</div>
                  <div class="latelyFollower">
                    <van-icon name="flower-o" class="follower" />
                    月票数：{{item.latelyFollower}}
                  </div>
                  <div class="ranking-tag one-text clearfix">
                    <div class="ranking-author one-text fl">作者：{{item.author}}</div>
                    <div class="fr tag">
                      <van-tag plain size="medium" type="danger">{{item.majorCate}}</van-tag>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="女生">
          <div class="maleRangk">
            <van-sidebar class="fl" v-model="activeKey">
              <van-sidebar-item
                v-for="(item,index) in tabData.female"
                :key="index"
                :title="item.shortTitle"
                :class="active == index ? 'active' : ''"
                @click="tabIndex(index)"
              />
            </van-sidebar>
            <div class="fr right">
              <div
                class="ranking-item clearfix"
                v-for="(item,index) in  femaleData"
                :key="index"
                @click="hotClick(item)"
              >
                <div class="ranking-img fl">
                  <img class="auto-img" v-lazy="item.cover" alt />
                </div>
                <div class="ranking-detail fl">
                  <div class="ranking-title one-text">
                    <van-tag
                      class="tag"
                      round
                      size="medium"
                      :type="index + 1 == 1 ?  'danger' : index + 1 == 2 ? 'warning' : index + 1 == 3 ? 'success' : 'default'"
                    >{{index + 1}}</van-tag>
                    {{item.title}}
                  </div>
                  <div class="ranking-shortIntro">简介：{{item.shortIntro}}</div>
                  <div class="latelyFollower">
                    <van-icon name="flower-o" class="follower" />
                    月票数：{{item.latelyFollower}}
                  </div>
                  <div class="ranking-tag one-text clearfix">
                    <div class="ranking-author one-text fl">作者：{{item.author}}</div>
                    <div class="fr tag">
                      <van-tag plain size="medium" type="danger">{{item.majorCate}}</van-tag>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <van-icon class="login" name="contact" @click="login" size="30px" />
      <van-icon class="book" name="orders-o" size="30px" @click="bookshelf" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0,
      tabData: [],
      maleData: [],
      femaleData: [],
      isSex: 0,
      active: 0
    };
  },
  created() {
    this.getHotBookClass(0, 0);
  },
  methods: {
    change(index) {
      this.isSex = index;
      this.getHotBookClass(0, this.isSex);
      if (index == 0 || index == 1) {
        this.activeKey = 0;
        this.active = 0;
      }
    },
    tabIndex(index) {
      this.active = index;
      this.activeKey = index;
      this.getHotBookClass(this.activeKey, this.isSex);
    },
    bookshelf() {
      this.$router.push({ name: "Bookshelf" });
    },
    hotClick(item) {
      let _id = item._id;
      this.$router.push({
        name: "Detail",
        query: { id: _id }
      });
    },
    getHotBookClass(activeKey, isSex) {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/rank-category"
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            let allData = [];
            let sex = [];
            let rankId = "";
            if (Boolean(isSex)) {
              allData.female = result.data.female.slice(0, 7);
              sex = "female";
              rankId = allData.female[activeKey]._id;
            } else {
              allData.male = result.data.male.slice(0, 7);
              sex = "male";
              rankId = allData.male[activeKey]._id;
            }
            this.tabData = allData;
            //
            this.getHotBookData(sex, rankId);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    getHotBookData(sex, id) {
      let data = [];
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/rank/" + id
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            for (let i = 0; i < result.data.ranking.books.length; i++) {
              result.data.ranking.books[i].cover = this.imgKey(
                result.data.ranking.books[i].cover
              );
            }
            data = result.data.ranking.books;
            data = data.sort(this.sortNumber);
            if (sex == "male") {
              this.maleData = data;
            } else {
              this.femaleData = data;
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    login() {
      this.$router.push({ name: "Login" });
    },
    sortNumber(a, b) {
      return b.latelyFollower - a.latelyFollower;
    },
    imgKey(value) {
      return unescape(value).slice(7);
    },
    //返回
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

